.block {
    width: 300px;
}

.product_block {
    text-decoration: none;
}

.product_block img{
    width: 300px;
    height: 300px;
    object-fit: cover;
}

.price_block {
    display: flex;
    gap: 30px;
}

.discont_price {
    font-weight: 600;
    font-size: 30px;
    line-height: 130%;
    letter-spacing: 0.03em;
    color: #000;
}

.price {
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.03em;
    text-decoration-line: line-through;
    color: #8B8B8B;
}

.discont {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.03em;
    color: #FF32A1;
}